import { BLOCKS } from '@contentful/rich-text-types';
import { documentToReactComponents } from '@contentful/rich-text-react-renderer';

import BlockQuote from './Blockquote';

const richTextOptions = {
  renderNode: {
    [BLOCKS.EMBEDDED_ASSET]: node => {
      const { title, description, file } = node.data.target.fields;
      const mimeType = file.contentType;
      const mimeGroup = mimeType.split('/')[0];

      switch (mimeGroup) {
        case 'image':
          return (
            <>
              <img
                title={title ? title : null}
                alt={description ? description : null}
                src={file.url}
              />
              <style jsx>{`
                img {
                  margin: 5px 0;
                }

                @media (min-width: 769px) {
                  img {
                    width: 47%;
                    margin: 12px 10px;
                  }
                }
              `}</style>
            </>
          );
        case 'application':
          return (
            <a alt={description ? description : null} href={file.url}>
              {title ? title : file.details.fileName}
            </a>
          );
        default:
          return (
            <span style={{ backgroundColor: 'red', color: 'white' }}>
              {' '}
              {mimeType} embedded asset{' '}
            </span>
          );
      }
    },
    [BLOCKS.EMBEDDED_ENTRY]: node => {
      const fields = node.data.target.fields;
      switch (node.data.target.sys.contentType.sys.id) {
        case 'blockquote':
          return (
            <div>
              <BlockQuote quoteText={fields.quoteText} quoter={fields.quoter} />
            </div>
          );
        default:
          return <div>??????????????? {title} </div>;
      }
    }
  }
};

const ContentfulRichText = function(text) {
  return documentToReactComponents(text, richTextOptions);
};

export default ContentfulRichText;
